:root {
    @each $key1,
    $value1 in $root {
        @each $key2,
        $value2 in $value1 {
            --#{$key2}: #{$value2};
        }
    }
    @each $key,
    $value in $breakpoints {
        --#{$key}: #{$value};
    }
}

* {
    scroll-behavior: smooth;
    text-size-adjust: 100%;
    @include transition();
}

body {
    overflow-x: hidden;
}

::-webkit-scrollbar {
    width: 6px;
    height: 8px;
}

::-webkit-scrollbar-track {
    background-color: var(--light-color, #fff);
    border-radius: 10px;
    -webkit-border-radius: 10px;
    -moz-border-radius: 10px;
    -ms-border-radius: 10px;
    -o-border-radius: 10px;
}

::-webkit-scrollbar-thumb {
    background-color: $secondary;
    border-radius: 10px;
    -webkit-border-radius: 10px;
    -moz-border-radius: 10px;
    -ms-border-radius: 10px;
    -o-border-radius: 10px;
}

img {
    user-select: none;
    object-fit: cover;
    -webkit-user-drag: none;
}

a {
    text-decoration: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    font-family: "Poppins", sans-serif;
}

p {
    font-family: "Rubik", sans-serif;
    font-size: 15px;
}

.dropdown-toggle {
    &::after {
        margin-left: 0.4em !important;
        vertical-align: 0.15em !important;
    }
}

.carousel-item::after {
    content: "";
    position: absolute;
    display: block;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.4);
    z-index: 1;
    cursor: pointer;
}

.carousel-control-prev {
    width: 8%;
    & &-icon {
        border-radius: 50%;
        -webkit-border-radius: 50%;
        -moz-border-radius: 50%;
        -ms-border-radius: 50%;
        -o-border-radius: 50%;
        background-size: 50%;
        background-color: $main;
    }
}

.carousel-control-next {
    width: 8%;
    & &-icon {
        border-radius: 50%;
        -webkit-border-radius: 50%;
        -moz-border-radius: 50%;
        -ms-border-radius: 50%;
        -o-border-radius: 50%;
        background-size: 50%;
        background-color: $main;
    }
}

.swiper-slide {
    height: initial;
    img {
        display: block;
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
}

.swiper-pagination-bullet-active {
    background: $main !important;
}

.card-img,
.card-img-top {
    width: 100%;
    object-fit: cover;
}

hr {
    height: 7px !important;
    opacity: 1 !important;
    border-radius: 50px;
    -webkit-border-radius: 50px;
    -moz-border-radius: 50px;
    -ms-border-radius: 50px;
    -o-border-radius: 50px;
}

.list-group-flush {
    >.list-group-item {
        border-color: #fff;
    }
}

// Below 1200px
@include mq("desktop", "max") {
    .carousel-control-prev,
    .carousel-control-next {
        width: 7%;
    }
}

// Below 1024px
@include mq("tablet-wide", "max") {
    .carousel-control-prev,
    .carousel-control-next {
        width: 9%;
    }
}